body {
    /* overflow-y: hidden; */
}

img {
    width: 100%;
    height: 100%;
    object-fit: contain;
}

.table-create-btn {
    position: absolute;
    right: 50px;
    bottom: 30px;
    cursor: pointer;
    background-color: var(--light-blue);
    border-radius: 50px;
    width: 50px;
    height: 50px;
    color: white;
    z-index: 1;
}

.input {
    border: 1px solid black;
    width: 250px;
    height: 30px;
    padding: 10px;
}

button {
    border: none;
    cursor: pointer;
}

.link {
    color: var(--green);
    text-decoration: underline;
    cursor: pointer;
}