.newContact {
  flex: 4;
  padding: 0 20px;
}

.addContactForm {
  display: flex;
  flex-wrap: wrap;
  margin-top: 10px;
  gap: 15px;
}

.addContactItem {
  width: 250px;
  display: flex;
  flex-direction: column;
}

.label-container {
  margin-bottom: 10px;
  display: flex;
  justify-content: space-between;
}

.label-container span {
  color: var(--green);
  text-decoration: underline;
  cursor: pointer;
}

.addContactItem>label {
  color: gray;
  font-weight: 600;
  margin-bottom: 10px;
}

.addContactItem>input {
  padding: 10px;
}

.addContactItem>select {
  padding: 10px;
}

.add-contact-button--container {
  display: flex;
  align-items: flex-end;
}

.addContactButton {
  margin-top: 10px;
  padding: 7px 10px;
  border: none;
  border-radius: 10px;
  background-color: darkblue;
  color: white;
  font-weight: 600;
  cursor: pointer;
}

.option-modal-inputs-container {
  margin: 10px 0;
  display: flex;
  justify-content: space-between;
  gap: 20px;
}