.input-wrapper {
    display: flex;
    align-items: center;
    gap: 20px;
}

.input-wrapper--column {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    gap: 10px;
}

.input-wrapper>label,
.input-wrapper--column>label {
    font-size: 14px;
    font-weight: 600;
    color: rgb(151, 150, 150);
}

.input-wrapper>input,
.input-wrapper--column>input {
    height: 10px;
    padding: 10px;
    border: 1px solid gray;
    border-radius: 5px;
}