.loader-animation {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 80px;
    height: 80px;
    z-index: 1000;
}

.loader-animation div {
    display: inline-block;
    width: 8px;
    height: 8px;
    border-radius: 50%;
    margin-right: 4px;
    animation: loader 0.8s ease-in-out infinite;
    background-color: black;
}

.loader-animation div:nth-child(1) {
    animation-delay: 0.1s;
}

.loader-animation div:nth-child(2) {
    animation-delay: 0.2s;
}

.loader-animation div:nth-child(3) {
    animation-delay: 0.3s;
}

.loader-animation div:nth-child(4) {
    animation-delay: 0.4s;
}

.loader-animation div:nth-child(5) {
    animation-delay: 0.5s;
}

.loader-animation div:nth-child(6) {
    animation-delay: 0.6s;
}

.loader-animation div:nth-child(7) {
    animation-delay: 0.7s;
}

.loader-animation div:nth-child(8) {
    animation-delay: 0.8s;
}

@keyframes loader {
    0% {
        transform: scale(0);
        opacity: 0.8;
    }

    50% {
        opacity: 0.4;
    }

    100% {
        transform: scale(1);
        opacity: 0.8;
    }
}