.btn-container {
    display: flex;
    align-items: center;
    justify-content: center;
    gap: var(--m-gap);
    margin: var(--m-gap) 0;

}

.btn {
    border: none;
    border-radius: 10px;
    padding: 5px 10px;
    cursor: pointer;
}

.primary-btn {
    background-color: var(--green-100);
    font-weight: 600;
    color: var(--white-100);
}

.secondary-btn {
    background-color: var(--red-50);
    font-weight: 500;
    color: var(--white-100);
}