.contact {
  flex: 4;
  padding: 20px;
}

.contactTitleContainer {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.contactAddButton {
  width: 80px;
  border: none;
  padding: 5px;
  background-color: teal;
  color: white;
  border-radius: 5px;
  font-size: 16px;
  cursor: pointer;
}

.contactTop {
  display: flex;
}

.contactTopLeft,
.contactTopRight {
  flex: 1;
}

.contactTopRight {
  padding: 20px;
  margin: 20px;
  -webkit-box-shadow: 0px 0px 15px -10px rgba(0, 0, 0, 0.75);
  box-shadow: 0px 0px 15px -10px rgba(0, 0, 0, 0.75);
}

.contactInfoImg {
  width: 40px;
  height: 40px;
  border-radius: 50%;
  object-fit: cover;
  margin-right: 20px;
}

.contactInfoTop {
  display: flex;
  align-items: center;
}

.contactName {
  font-weight: 600;
}

.contactInfoBottom {
  margin-top: 10px;
}

.contactInfoItem {
  width: 150px;
  display: flex;
  justify-content: space-between;
}

.contactInfoValue {
  font-weight: 300;
}

.contactBottom {
  padding: 20px;
  margin: 20px;
  -webkit-box-shadow: 0px 0px 15px -10px rgba(0, 0, 0, 0.75);
  box-shadow: 0px 0px 15px -10px rgba(0, 0, 0, 0.75);
}

.contactForm {
  display: flex;
  gap: 20px;
}

.contactFormLeft {
  display: flex;
  flex-direction: column;
}

.contactFormLeft>label {
  margin-bottom: 10px;
  color: gray;
}

.contactFormLeft>input {
  margin-bottom: 10px;
  border: none;
  padding: 5px;
  border-bottom: 1px solid gray;
}

.contactFormLeft>select {
  margin-bottom: 10px;
}

.contactUploadImg {
  width: 100px;
  height: 100px;
  border-radius: 10px;
  object-fit: cover;
  margin-right: 20px;
}

.contactFormRight {
  display: flex;
  flex-direction: column;
  justify-content: space-around;
}

.contactUpload {
  display: flex;
  align-items: center;
}

.contactButton {
  border: none;
  padding: 5px;
  border-radius: 5px;
  background-color: darkblue;
  color: white;
  font-weight: 600;
  cursor: pointer;
}